import React, { Component } from 'react';
import './Block.scss';
import './Item.scss';
import './Input.scss';

class Item extends Component {
  constructor(props) {
    super(props);
    this.state = {
      active: false,
    }
    this.handleClick = this.handleClick.bind(this);
  }

  componentWillMount() {
    this.setState({
      active_anyway: this.props.active_anyway
    })
  }

  handleClick() {
    this.setState({
      active: !this.state.active,
    })

    if (this.state.active_anyway !== undefined) {
      this.setState({
        active_anyway: !this.state.active_anyway
      })
    }

    console.log(this.state);

    this.props.updateDisplay(this.props.value);
  }

  render() {
    return (
      <div className={(this.state.active_anyway || (this.state.active && this.props.active)) ? "item active" : "item"} onClick={this.handleClick} style={this.props.font ? {fontFamily: this.props.font} : {fontFamily: "'Nunito Sans', sans-serif"}}>{this.props.value}</div>
    );
  }
}

class Input extends Component {
  constructor(props) {
    super(props);
    this.state = {
      value: ''
    }
    this.handleClick = this.handleClick.bind(this);
    this.handleKeyPress = this.handleKeyPress.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }
  
  handleChange(e) {
    this.setState({
      value: e.target.value
    })
  }

  handleClick() {
    if (this.state.value !== '') {
      this.props.updateInput(this.state.value);
      this.setState({
        value: ''
      })
    }
  }
  
  handleKeyPress(e) {
    if (e.key === 'Enter' && this.state.value !== '') {
      this.props.updateInput(this.state.value);
      this.setState({
        value: ''
      })
    }
  }

  render() {
    return (
      <div className="input-container">
        <input className="keyword-input" type="text" placeholder="Nhập từ khóa bạn muốn theo dõi" onChange={this.handleChange} onKeyPress={this.handleKeyPress} value={this.state.value}/>
        <button className="add" onClick={this.handleClick} />
      </div>
    );
  }
}

class Block extends Component {
  constructor(props) {
    super(props);
    this.state = {
      active: '',
      list: [],
    }
    this.updateDisplay = this.updateDisplay.bind(this);
    this.updateInput = this.updateInput.bind(this);
  }

  updateDisplay(value) {
    this.setState({
      active: value
    });

    console.log(value);
  }

  updateInput(value) {
    this.setState({
      list: [...this.state.list, value]
    })
  }

  render() {
    return (
      <div className="block">
        <div className="block-title">{this.props.value}</div>
        <div className="block-content">
          {
            this.props.input ? <React.Fragment>
              <Input updateInput={this.updateInput}/> 
              {
                this.state.list.map((el, i) => {
                  return <Item key={i} value={el} updateDisplay={this.updateDisplay} active_anyway={true} />
                })
              }
            </React.Fragment>
              : this.props.unique ? this.props.items.map((el, i) => {
                if (el === this.state.active || el.value === this.state.active) {
                  return <Item key={i} value={el.value ? el.value : el} updateDisplay={this.updateDisplay} active={true} font={el.font ? el.font : false}/>
                }
                else {
                  return <Item key={i} value={el.value ? el.value : el} updateDisplay={this.updateDisplay} active={false}  font={el.font ? el.font : false}/>
                }
                }) :
                  this.props.items.map((el, i) => {
                      return <Item key={i} value={el} updateDisplay={this.updateDisplay} active={true}/>
                  })
          }
        </div>
      </div>
    );
  }
}

export default Block;