import React from 'react';
import './Options.scss';

function Options() {
  return (
    <section className="options" id="options">
        <div className="options-wrapper">
        <div className="title">Trải nghiệm TikiCUSTOM ngay hôm nay</div>
        <div className="blocks">
          <div className="block">
              <div className="block-image"><img src="./assets/icons/wishlist.png" width="170" /></div>
              <div className="block-title">Theo dõi sản phẩm<br />yêu thích của bạn</div>
              <div className="block-content">Tự do lựa chọn: <b>Danh mục sản phẩm, thương hiệu, từ khóa quan tâm,...</b></div>
              <a href="#product" className="block-link">Xem chi tiết</a>
            </div>
            <div className="block">
              <div className="block-image"><img src="./assets/icons/appearance.png" width="150" /></div>
              <div className="block-title">Tùy biến giao diện<br />theo ý muốn</div>
              <div className="block-content">Thoải mái thay đổi: <b>Bố cục, màu sắc, font chữ,...</b></div>
              <a href="#appearance" className="block-link">Xem chi tiết</a>
            </div>
        </div>
      </div>
    </section>
  );
}

export default Options;