import React, { Component } from 'react';
import './Product.scss';
import Block from '../Block/Block';

const porfolio = ['Mặc định', 'Khuyến mãi HOT', 'Điện thoại - Máy tính bảng', 'Điện tử - Điện lạnh', 'Phụ kiện - Thiết bị số', 'Laptop - Thiết bị IT', 'Máy ảnh - Quay phim', 'Điện gia dụng', 'Nhà cửa đời sống', 'Hàng tiêu dùng - Thực phẩm', ' Đồ chơi, Mẹ & bé', 'Làm đẹp - Sức khỏe', 'Thời trang - Phục kiện', 'Thể thao - Dã ngoại', 'Xe máy, Ô tô, Xe đạp', 'Hàng quốc tế', 'Sách, VPP & Quà tặng', 'Voucher - Dịch vụ - Thẻ cào']

const offers = ['Mặc định', 'TikiNOW - Giao hàng 2h', 'TikiNGON - Thực phẩm tươi sống', 'Sản phẩm chính hãng', '30 ngày đổi trả'];

function Product() {
  return (
    <section className="options product" id="product">
        <div className="options-wrapper">
        <div className="title">Sản phẩm quan tâm</div>
        <div className="blocks">
          <Block value="Danh mục sản phẩm" items={ porfolio } name="porfolio" />
          <Block value="Chương trình áp dụng" items={ offers } name="offers" />
          <Block value="Từ khóa quan tâm" input={true}  name="keywords" />
        </div>
      </div>
    </section>
  );
}

export default Product;