import React from 'react';
import './Appearance.scss';
import Block from '../Block/Block';


const fonts = [
  {
    value: 'Mặc định',
    font: "'Roboto', sans-serif"
  },
  {
    value: 'Nunito Sans',
    font: "'Nunito Sans', sans-serif",
  }, {
    value: 'Yellowtail',
    font: "'Yellowtail', cursive",
  }, {
    value: 'Arvo',
    font: "'Arvo', serif",
  }, {
    value: 'Playfair Display',
    font: "'Playfair Display', serif",
  }, {
    value: 'Titillium Web',
    font: "'Titillium Web', sans-serif",
  }, {
    value: 'Dosis',
    font: "'Dosis', sans-serif",
  }, {
    value: 'Alegreya',
    font: "'Alegreya', serif",
  }, {
    value: 'Open Sans',
    font: "'Open Sans', sans-serif",
  },
]

function Appearance() {
  return (
    <section className="options appearance" id="appearance">
      <div className="options-wrapper">
        <div className="title">Tùy biến giao diện</div>
        <div className="blocks">
          <Block value="Bố cục" items={ ['Mặc định', 'Tối giản'] } unique={ true }/>
          <Block value="Màu sắc" items={ ['Mặc định', 'Đen', 'Trắng', 'Đỏ', 'Hồng', 'Cam', 'Vàng', 'Lục', 'Lam', 'Chàm', 'Tím'] } unique={ true }/>
          <Block value="Font chữ" items={ fonts } unique={ true }/>
        </div>
      </div>
    </section>
  );
}

export default Appearance;