import React from 'react';
import './Intro.scss';

function Intro() {
  return (
    <section className="intro" id="intro">
        <header class="mobile-header">
        <button class="btn-back">
            <svg stroke="currentColor" fill="currentColor" stroke-width="0" viewBox="0 0 24 24" size="30" height="30" width="30" xmlns="http://www.w3.org/2000/svg">
                <path d="M15.41 7.41L14 6l-6 6 6 6 1.41-1.41L10.83 12z"></path>
            </svg>
        </button>
        <header class="mobile-header__title">TikiCUSTOM - Cá nhân hóa theo người dùng</header>
        </header>
        <header>
            <nav>
                <ul>
                    <li><a href="#">TRANG CHỦ</a></li>
                    <li><a href="#">HƯỚNG DẪN</a></li>
                    <li><a href="#">LIÊN HỆ</a></li>
                    <li><a href="#" role="button">ĐĂNG NHẬP</a></li>
                </ul>
            </nav>
        </header>
        <div className="container">
            <div className="wrapper">
                <div className="left">
                    <a href="#"><img src="./assets/images/logo.png" alt="logo" /></a>
                    <h1>TikiCUSTOM - Đặt trải nghiệm người dùng lên hàng đầu</h1>
                    <p>Với sự thấu hiểu khách hàng về nhu cầu cá nhân hóa trang web, Tiki đem đến tính năng TikiCUSTOM cho người dùng tùy chỉnh trang web phù hợp với sở thích của mình.</p>
                    <a href="#options" role="button">BẮT ĐẦU NGAY</a>
                </div>
                <div className="right">
                    <img src="./assets/images/Group_28.png" height="563px" alt="" />
                </div>
            </div>
        </div>
    </section>
  );
}

export default Intro;